<template lang="pug">
#FeatureInfo
    div.ba.b--gray.pa2.br2.mt1.shadow-1.box(:class="{ [p.Care_Type.replace(' ', '')]: true }" v-if="feature")
        .f6.b.mt1.Care_Type(:class="{ [p.Care_Type.replace(' ', '')]: true }") {{ careTypeLong }}
        h1.f4.tc.bb.b--black-20 {{ p.Service_Name }}
        table
            tr(v-for="([key, title]) of fields" v-if="p[key]")
                th.pt2.f6.tl.v-top {{ title }}
                td.pt2.v-top.pl2.f6.fw1 {{ p[key] }}
        //- h2.f5 Contact details
        //- table
        //-     tr(v-for="([key, title]) of contactFields" v-if="p[key]")
        //-         td.tr.v-top {{ title }}
        //-         td.v-top.pl2.f6.w1(:class="{ ['key_' + key]: true }") {{ p[key] }}
        //-     tr(v-if="p.Service_Email")
        //-         td(colspan="2") Email
        //-     tr(v-if="p.Service_Email")
        //-         td(colspan="2").key_Service_Email {{ p.Service_Email }}
        //-     tr(v-if="p.Service_Email")
        //-         td(colspan="2") Email
        //-     tr(v-if="p.Address")
        //-         td(colspan="2").key_Address {{ p.Address }}


        table.w5.mt1
            template(v-for="([key, title]) of contactFields" v-if="p[key]")
                tr
                    th.f6.tl {{ title }}
                tr
                    td.v-top.f6.w1.pb2.fw1(:class="{ ['key_' + key]: true }") {{ p[key] }}

        .tc.mobile-only
            .link.dim.ph3.pv2.mb2.dib.white.bg-dark-blue.pointer(@click="showOnMap") Show on map

    div(v-else)
        //- h2 Click a site for more information.


</template>

<script>
import EventBus from '../EventBus';
export default {
    name: 'FeatureInfo',
    data: () => ({
        feature: undefined,
        ignoreProps: ['id', 'Longitude', 'Latitude', 'image_url'],
        fields: [
            ['MaxPlaces', 'Places'],
            ['Location', 'Service_Location'],
            ['Service_Status', 'Status'],
            ['Service_Location', 'Type'],
            ['Service_ID', 'ID'],
            ['phone', 'Phone number'],
            ['mobile', 'Mobile'],
        ],
        contactFields: [
            ['Service_Email', 'Email'],
            ['Address', 'Address'],
        ],
    }),
    computed: {
        imageUrl() {
            return this.feature && this.feature.properties.image_url;
        },
        p() {
            if (!this.feature) {
                return { Care_Type: '' };
            } else {
                return {
                    ...this.feature.properties,
                    mobile: formatMobile(
                        this.feature.properties.Service_Mobile_No
                    ),
                    phone: formatPhone(
                        this.feature.properties.Service_Phone_No
                    ),
                };
            }
        },
        careTypeLong() {
            return {
                OSHC: 'Outside School Hours Care',
                LDC: 'Long Day Care',
                KGN: 'Kindergarten',
            }[this.p.Care_Type];
        },
    },
    created() {
        window.FeatureInfo = this;
    },
    methods: {
        showOnMap() {
            EventBus.$emit('toggle-sidebar', false);
            EventBus.$emit('highlight-site', this.site);
        },
    },
};

function formatMobile(m) {
    if (!m) {
        return;
    }
    if (m.length === 9) {
        m = '0' + m;
    }
    return m.replace(/(\d\d\d\d)(\d\d\d)(\d\d\d)/, '$1 $2 $3');
}

function formatPhone(no) {
    if (!no) {
        return;
    }
    no = no.replace(/(3)(\d\d\d\d)(\d\d\d\d)/, '0$1 $2 $3');
    if (no.length === 9) {
        return formatMobile(no);
    }
    return no;
    //     m = '0' + m;
    // }
    // return m.replace(/(\d\d\d\d)(\d\d\d)(\d\d\d)/, '$1 $2 $3');
}
</script>

